import React from 'react'
import { DefaultLinkFactory, DefaultLinkModel } from '@projectstorm/react-diagrams'
import { NwAddLinkWidget, NwDelLinkWidget } from './NwAddLinkWidget'

export class NwAddLinkFactory extends DefaultLinkFactory {
  constructor() {
    super('default')
  }
  generateLinkSegment(model: DefaultLinkModel, selected: boolean, path: string) {
    return (
      <g>
        <NwAddLinkWidget model={model} path={path} />
      </g>
    )
  }
}
export class NwDelLinkFactory extends DefaultLinkFactory {
  constructor() {
    super('default')
  }
  generateLinkSegment(model: DefaultLinkModel, selected: boolean, path: string) {
    return (
      <g>
        <NwDelLinkWidget model={model} path={path} selected={selected} />
      </g>
    )
  }
}
