import React, { useEffect } from 'react'
import {
  Alert,
  Box,
  Button,
  Divider,
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Paper,
  Select,
  SelectChangeEvent,
  Stack,
  Typography,
} from '@mui/material'
import createEngine, { DiagramModel } from '@projectstorm/react-diagrams'
import { AddWidget } from 'custom/AddWidget'
import { NwAddLinkFactory } from 'custom/NwAddLinkFactory'

interface AddProps {
  tabChange: React.Dispatch<React.SetStateAction<number>>
}
// eslint-disable-next-line no-var
const engine = createEngine({
  registerDefaultPanAndZoomCanvasAction: false,
  registerDefaultZoomCanvasAction: true,
  registerDefaultDeleteItemsAction: false,
})
engine.getLinkFactories().registerFactory(new NwAddLinkFactory())
engine.setMaxNumberPointsPerLink(1)
// eslint-disable-next-line no-var
const model = new DiagramModel()
engine.setModel(model)

export function AddConnection(props: AddProps) {
  const [portOut, setPortOut] = React.useState('')
  const [portIn, setPortIn] = React.useState('')
  const [device, setDevice] = React.useState('')
  const [portInActive, setPortInActive] = React.useState<boolean>(false)
  const [addBtnActive, setAddBtnActive] = React.useState<boolean>(false)

  const handleDeviceChange = (ev: SelectChangeEvent) => {
    const val = ev.target.value
    setPortInActive(val !== '')
    setDevice(val)
    if (!val) {
      setPortIn('')
    }
  }
  const handleAddBtn = () => {
    setPortOut('')
    setPortIn('')
    setDevice('')
    model.getNodes().map((elm) => {
      model.removeNode(elm)
    })
    model.getLinks().map((elm) => {
      model.removeLink(elm)
    })
    props.tabChange(0)
  }
  useEffect(() => {
    setAddBtnActive(device != '' && portIn != '' && portOut != '')
  }, [portIn, portOut, device])

  return (
    <Stack direction="column" py={1}>
      <Alert severity="info" icon={false} sx={{ mx: 2 }}>
        
      </Alert>
      {/* <Typography variant="subtitle1" mx={1} color={'#666'}>
        
      </Typography> */}
      <Stack direction="row" spacing={2} p={2}>
        <Box p={2} component={Paper} sx={{ maxWidth: 300, flexGrow: 1 }}>
          <Typography variant="h6">1 ()</Typography>
          <Typography variant="body1">#D-1</Typography>
          <Typography variant="body1">Ohashi</Typography>
          <Typography variant="body1">/: 16/24</Typography>
          <Divider variant="fullWidth" sx={{ my: 2 }} />
          <FormControl sx={{ my: 1, minWidth: 150 }}>
            <InputLabel id="select-helper-label-1">Port</InputLabel>
            <Select
              labelId="select-helper-label-1"
              variant="outlined"
              size="small"
              value={portOut}
              label="Port"
              onChange={(ev: SelectChangeEvent) => setPortOut(ev.target.value)}
            >
              <MenuItem value={''}>
                <em>None</em>
              </MenuItem>
              <MenuItem value="Port-1">Port-1</MenuItem>
              <MenuItem value="Port-2">Port-2</MenuItem>
              <MenuItem value="Port-3">Port-3</MenuItem>
            </Select>
            <FormHelperText></FormHelperText>
          </FormControl>
          <Divider variant="fullWidth" sx={{ my: 2 }} />
          <Stack spacing={2}>
            <Button variant="contained" disabled={!addBtnActive} onClick={handleAddBtn}>
              
            </Button>
            <Button variant="outlined" onClick={handleAddBtn}>
              
            </Button>
          </Stack>
        </Box>
        <Box p={2} component={Paper} sx={{ maxWidth: 300, flexGrow: 1 }}>
          <Typography variant="h6">2 ()</Typography>
          {/* <Typography variant="body1">#D-2</Typography> */}
          <FormControl sx={{ my: 1, minWidth: 260 }}>
            <InputLabel id="select-device-label-2"></InputLabel>
            <Select
              labelId="select-device-label-2"
              variant="outlined"
              size="medium"
              value={device}
              label=""
              onChange={handleDeviceChange}
            >
              <MenuItem value="">
                <em></em>
              </MenuItem>
              <MenuItem value="#T-1">#T-1</MenuItem>
              <MenuItem value="#T-2">#T-2</MenuItem>
              <MenuItem value="#T-3">#T-3</MenuItem>
            </Select>
            {/* <FormHelperText></FormHelperText> */}
          </FormControl>
          {/* <Typography variant="body1">Ohashi</Typography>
          <Typography variant="body1">/: 20/24</Typography> */}
          <Divider variant="fullWidth" sx={{ my: 2 }} />
          <FormControl sx={{ my: 1, minWidth: 150 }}>
            <InputLabel id="select-helper-label-2">Port</InputLabel>
            <Select
              labelId="select-helper-label-2"
              variant="outlined"
              size="small"
              label="Port"
              value={portIn}
              disabled={!portInActive}
              onChange={(ev: SelectChangeEvent) => setPortIn(ev.target.value)}
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              <MenuItem value="Port-1">Port-1</MenuItem>
              <MenuItem value="Port-2">Port-2</MenuItem>
              <MenuItem value="Port-3">Port-3</MenuItem>
            </Select>
            <FormHelperText></FormHelperText>
          </FormControl>
        </Box>
        <AddWidget device={device} portIn={portIn} portOut={portOut} engine={engine} model={model} />
      </Stack>
    </Stack>
  )
}
