import * as React from 'react'
import {
  DefaultLinkModel,
  DefaultNodeModel,
  DefaultPortModel,
  DiagramEngine,
  DiagramModel,
} from '@projectstorm/react-diagrams'
import { CanvasWidget } from '@projectstorm/react-canvas-core'
import { Box } from '@mui/material'

interface WidgetProps {
  portOut: string
  portIn: string
  device: string
  engine: DiagramEngine
  model: DiagramModel
}
interface DelWidgetProps {
  device: string
  link: string
  engine: DiagramEngine
  model: DiagramModel
}
