export function AddWidget(props: WidgetProps) {
  const { portOut, portIn, device, engine, model } = props
  const [srcNode, setSrcNode] = React.useState<DefaultNodeModel | null>(null)
  const [node2, setNode2] = React.useState<DefaultNodeModel | null>(null)
  const [portInModel, setPortInModel] = React.useState<DefaultPortModel | null>(null)
  const [portOutModel, setPortOutModel] = React.useState<DefaultPortModel | null>(null)
  const [link, setLink] = React.useState<DefaultLinkModel | null>(null)
  React.useEffect(() => {
    if (model.getNodes().length === 0) {
      const node1 = new DefaultNodeModel({
        name: '1#10-A',
        color: '#00bfff',
      })
      node1.setPosition(100, 100)
      node1.addOutPort('Port-1')
      node1.addOutPort('Port-2')
      node1.addOutPort('Port-3')
      model.addNode(node1)
      setSrcNode(node1)
    }
    if (device != '') {
      if (!node2 || (node2 && node2.getOptions().name !== device)) {
        const newNode = new DefaultNodeModel({
          name: device,
          color: '#9400d3',
        })
        newNode.setPosition(400, 100)
        if (node2) model.removeNode(node2)
        model.addNode(newNode)
        setNode2(newNode)
      }
    } else {
      if (node2) {
        model.removeNode(node2)
        setNode2(null)
        setPortInModel(null)
        if (link) {
          model.removeLink(link)
          setLink(null)
        }
      }
    }
    let newPortIn = portInModel
    if (portIn) {
      if (node2?.getInPorts().length == 0 || (node2 && portInModel?.getName() !== portIn)) {
        const newPort = node2.addInPort(portIn)
        if (portInModel && node2.getInPorts().length > 0) node2.removePort(portInModel)
        setPortInModel(newPort)
        newPortIn = newPort
      }
    } else {
      if (node2 && portInModel) {
        node2.removePort(portInModel)
        setPortInModel(null)
        newPortIn = null
      }
    }
    let newPortOut = portOutModel
    if (portOut) {
      if (!portOutModel || portOutModel.getName() !== portOut) {
        srcNode?.getOutPorts().map((elm) => {
          if (elm.getName() === portOut) {
            setPortOutModel(elm)
            newPortOut = elm
            return
          }
        })
      }
    } else {
      if (srcNode && portOutModel) {
        setPortOutModel(null)
        newPortOut = null
      }
    }

    if (portIn && portOut) {
      if (
        newPortOut &&
        newPortIn &&
        (!link || portOutModel?.getName() !== portOut || portInModel?.getName() !== portIn)
      ) {
        const newLink = newPortOut.link<DefaultLinkModel>(newPortIn)
        // newLink.addLabel()
        if (link) model.removeLink(link)
        model.addAll(newLink)
        setLink(newLink)
      }
    } else {
      if (link) {
        model.removeLink(link)
        setLink(null)
      }
    }
    // engine.repaintCanvas()
  }, [portIn, portOut, device])

  return (
    <Box id="canvas-sample-1" sx={{ maxWidth: 800, flexGrow: 1 }}>
      <CanvasWidget className="diagram-container grid-box" engine={engine} />
    </Box>
  )
}
interface Link {
  key: string
  model: DefaultLinkModel
  linkId: string
}
type LinkObj = {
  [key: string]: Link
}
export function DelWidget(props: DelWidgetProps) {
  const { device, link, engine, model } = props
  const [srcNode, setSrcNode] = React.useState<DefaultNodeModel | null>(null)
  const [dstNode, setDstNode] = React.useState<DefaultNodeModel | null>(null)
  const [selectedLink, setSelectedLink] = React.useState('')
  const [links, setLinks] = React.useState<LinkObj>({})
  React.useEffect(() => {
    if (model.getNodes().length === 0) {
      const node1 = new DefaultNodeModel({
        name: '1#10-A',
        color: '#00bfff',
      })
      node1.setPosition(100, 100)
      node1.addOutPort('Port-1')
      node1.addOutPort('Port-2')
      node1.addOutPort('Port-3')
      model.addNode(node1)
      setSrcNode(node1)
    }
    if (device != '') {
      if (!dstNode || (dstNode && dstNode.getOptions().name !== device)) {
        const newNode = new DefaultNodeModel({
          name: device,
          // color: '#b0c4de',
          color: '#7fff00',
        })
        newNode.setPosition(400, 100)
        const newLinks: LinkObj = {}
        srcNode?.getOutPorts().map((out, idx) => {
          const num = idx + 1
          const inPort = newNode.addInPort(`Port-${num}`)
          const newLink = out.link<DefaultLinkModel>(inPort)
          const newObj: Link = {
            key: `connection-${num}`,
            model: newLink,
            linkId: newLink.getID(),
          }
          newLinks[newObj.key] = newObj
        })
        if (dstNode) {
          model.removeNode(dstNode)
          Object.keys(links).forEach((key) => {
            model.removeLink(links[key].model)
          })
          if (selectedLink) {
            setSelectedLink('')
          }
        }
        model.addNode(newNode)
        Object.keys(newLinks).forEach((key) => {
          model.addLink(newLinks[key].model)
        })
        setDstNode(newNode)
        setLinks(newLinks)
      }
    } else {
      if (dstNode) {
        model.removeNode(dstNode)
        Object.keys(links).forEach((key) => {
          model.removeLink(links[key].model)
        })
        setDstNode(null)
        setLinks({})
        if (selectedLink) {
          setSelectedLink('')
        }
      }
    }
    if (link) {
      if (!selectedLink || selectedLink !== link) {
        setSelectedLink(link)
        if (Object.keys(links).includes(link)) {
          const selectedLinkId = links[link].linkId
          model.getLinks().map((elm) => {
            elm.setSelected(elm.getID() === selectedLinkId)
          })
        }
      }
    } else {
      setSelectedLink('')
      model.getLinks().map((elm) => {
        elm.setSelected(false)
      })
    }
    // engine.repaintCanvas()
  }, [device, link])

  return (
    <Box id="canvas-sample-1" sx={{ maxWidth: 800, flexGrow: 1 }}>
      <CanvasWidget className="diagram-container grid-box" engine={engine} />
    </Box>
  )
}
