import { DefaultLinkModel } from '@projectstorm/react-diagrams'
import React from 'react'

export class NwAddLinkWidget extends React.Component<{
  model: DefaultLinkModel
  path: string
  color?: string
}> {
  render() {
    return (
      <path
        fill="none"
        strokeWidth={4} // 
        stroke={this.props.color ? this.props.color : '#00bfff'} // 
        d={this.props.path}
        className="nw-add-link"
      />
    )
  }
}
export class NwDelLinkWidget extends React.Component<{
  model: DefaultLinkModel
  path: string
  selected: boolean
  color?: string
}> {
  render() {
    return (
      <path
        fill="none"
        strokeWidth={4} // 
        stroke={this.props.selected ? '#dc143c' : '#696969'}
        d={this.props.path}
        className={this.props.selected ? 'nw-del-link-selected' : 'nw-del-link'}
      />
    )
  }
}
