import React, { useEffect } from 'react'
import {
  Alert,
  Box,
  Button,
  Divider,
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Paper,
  Select,
  SelectChangeEvent,
  Stack,
  Typography,
} from '@mui/material'
import createEngine, { DiagramModel } from '@projectstorm/react-diagrams'
import { DelWidget } from 'custom/AddWidget'
import { NwDelLinkFactory } from 'custom/NwAddLinkFactory'

interface DelProps {
  tabChange: React.Dispatch<React.SetStateAction<number>>
}
// eslint-disable-next-line no-var
const engine = createEngine({
  registerDefaultPanAndZoomCanvasAction: false,
  registerDefaultZoomCanvasAction: true,
  registerDefaultDeleteItemsAction: false,
})
engine.getLinkFactories().registerFactory(new NwDelLinkFactory())
engine.setMaxNumberPointsPerLink(0)
// eslint-disable-next-line no-var
const model = new DiagramModel()
engine.setModel(model)

export function DelConnection(props: DelProps) {
  const [conn, setConn] = React.useState('')
  const [device, setDevice] = React.useState('')
  const [connActive, setConnActive] = React.useState<boolean>(false)
  const [delBtnActive, setDelBtnActive] = React.useState<boolean>(false)

  const handleDeviceChange = (ev: SelectChangeEvent) => {
    const val = ev.target.value
    setConnActive(val !== '')
    setDevice(val)
    if (!val) {
      setConn('')
    }
  }
  const handleDelBtn = () => {
    setDevice('')
    setConn('')
    model.getNodes().map((elm) => {
      model.removeNode(elm)
    })
    model.getLinks().map((elm) => {
      model.removeLink(elm)
    })
    props.tabChange(0)
  }
  useEffect(() => {
    setDelBtnActive(device != '' && conn != '')
  }, [device, conn])

  return (
    <Stack direction="column" py={1}>
      <Alert severity="info" icon={false} sx={{ mx: 2 }}>
        
      </Alert>
      <Stack direction="row" spacing={2} p={2}>
        <Box p={2} component={Paper} sx={{ maxWidth: 300, flexGrow: 1 }}>
          <Typography variant="h6">1 ()</Typography>
          <Typography variant="body1">#D-1</Typography>
          <Typography variant="body1">Ohashi</Typography>
          <Typography variant="body1">/: 16/24</Typography>
          <Divider variant="fullWidth" sx={{ my: 2 }} />
          <Stack spacing={2}>
            <Button variant="contained" disabled={!delBtnActive} onClick={handleDelBtn}>
              
            </Button>
            <Button variant="outlined" onClick={handleDelBtn}>
              
            </Button>
          </Stack>
        </Box>
        <Box p={2} component={Paper} sx={{ maxWidth: 300, flexGrow: 1 }}>
          <Typography variant="h6">2 ()</Typography>
          {/* <Typography variant="body1">#D-2</Typography> */}
          <FormControl sx={{ my: 1, minWidth: 260 }}>
            <InputLabel id="select-device-label-2"></InputLabel>
            <Select
              labelId="select-device-label-2"
              variant="outlined"
              size="medium"
              value={device}
              label=""
              onChange={handleDeviceChange}
            >
              <MenuItem value="">
                <em></em>
              </MenuItem>
              <MenuItem value="#T-1">#T-1</MenuItem>
              <MenuItem value="#T-2">#T-2</MenuItem>
              <MenuItem value="#T-3">#T-3</MenuItem>
            </Select>
          </FormControl>
          <Divider variant="fullWidth" sx={{ my: 2 }} />
          <FormControl sx={{ my: 1, minWidth: 260 }}>
            <InputLabel id="select-helper-label-2">Connection</InputLabel>
            <Select
              labelId="select-helper-label-2"
              variant="outlined"
              size="small"
              label="Connection"
              value={conn}
              disabled={!connActive}
              onChange={(ev: SelectChangeEvent) => setConn(ev.target.value)}
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              <MenuItem value="connection-1"> (Port1 -- Port1)</MenuItem>
              <MenuItem value="connection-2"> (Port2 -- Port2)</MenuItem>
              <MenuItem value="connection-3"> (Port3 -- Port3)</MenuItem>
            </Select>
            <FormHelperText></FormHelperText>
          </FormControl>
        </Box>
        <DelWidget device={device} link={conn} engine={engine} model={model} />
      </Stack>
    </Stack>
  )
}
