___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.diagram-container {
  /* background: #add9f7; */
  /* background: #fafafa; */
  background: #eef9ff;
  width: 100%;
  height: 80vh;
}

.grid-box {
  /*  */
  background-image: linear-gradient(0deg, transparent calc(100% - 1px), #dfdfdf calc(100% - 1px)),
    linear-gradient(90deg, transparent calc(100% - 1px), #dfdfdf calc(100% - 1px));
  background-size: 24px 24px;
  background-repeat: repeat;
  background-position: center center;
  border: 2px solid #d3d3d3;
  border-radius: 5px;
}

.pointer-events-all {
  pointer-events: all;
}

@keyframes anime-linking {
  from {
    stroke-dashoffset: 24;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.nw-add-link {
  stroke-dasharray: 10, 2;
  animation: anime-linking 1s linear infinite;
  fill: none;
  pointer-events: all;
  /* pointer-events: auto; */
}
.nw-del-link {
  fill: none;
  pointer-events: all;
}
.nw-del-link-selected {
  stroke-dasharray: 10, 2;
  animation: anime-linking 1s linear infinite;
  fill: none;
  pointer-events: all;
  /* pointer-events: auto; */
}

.custom-node {
  border: solid 2px gray;
  border-radius: 5px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  position: relative;
}

.custom-node-color {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  transform: translate(-50%, -50%);
  border-radius: 10px;
}

.circle-port {
  width: 12px;
  height: 12px;
  margin: 2px;
  border-radius: 4px;
  background: darkgray;
  cursor: pointer;
}

.circle-port:hover {
  background: mediumpurple;
}
